var record : {id:string, title: string, author: string, rating: string, detail: string}[] = [
    {id: '0', title:"kam", author:"a", rating:'4', detail: "kam"},
    {id: '1', title:"kam", author:"a", rating:'4', detail: "kam"},
    {id: '2', title:"kam", author:"a", rating:'4', detail: "kam"},
    {id: '3', title:"kam", author:"b", rating:'4', detail: "kam"},
    {id: '4', title:"kam", author:"a", rating:'4', detail: "kam"},
    {id: '5', title:"kam", author:"a", rating:'4', detail: "kam"},
    {id: '6', title:"kam", author:"a", rating:'4', detail: "kam"},
    {id: '7', title:"kam", author:"a", rating:'4', detail: "kam"}

];

function showList(){
    var table = document.getElementById("myTable") as HTMLTableElement;
    var row, ceil1, ceil2, ceil3, ceil4, ceil0;
    for (let i = 0; i < record.length; i++) {
        row = table.insertRow(1);
        ceil0 = row.insertCell(0);
        ceil1 = row.insertCell(1);
        ceil2 = row.insertCell(2)
        ceil3 = row.insertCell(3);
        ceil4 = row.insertCell(4);

        ceil0.innerHTML = record[i].id;
        ceil1.innerHTML = record[i].title;
        ceil2.innerHTML = record[i].author;
        ceil3.innerHTML = record[i].rating;
        ceil4.innerHTML = record[i].detail;
        
    }
}
window.onload = showList;

