function searchListById(){
    var form = document.querySelector('.mySearch') as HTMLFormElement;
    var table = document.getElementById("myTable") as HTMLTableElement;
    for (let index = 0; index < record.length; index++) {
        row = table.deleteRow(1);
    }
    var id = document.querySelector("#search") as HTMLSelectElement;
    var row, ceil1, ceil2, ceil3, ceil4, ceil0;
    form.addEventListener('submit', (e: Event) =>{
        e.preventDefault();
        for (let i = 0; i < record.length; i++) {
            if (record[i].id == id.value){
                row = table.insertRow(1);
                ceil0 = row.insertCell(0);
                ceil1 = row.insertCell(1);
                ceil2 = row.insertCell(2)
                ceil3 = row.insertCell(3);
                ceil4 = row.insertCell(4);

                ceil0.innerHTML = record[i].id;
                ceil1.innerHTML = record[i].title;
                ceil2.innerHTML = record[i].author;
                ceil3.innerHTML = record[i].rating;
                ceil4.innerHTML = record[i].detail;
            }
        }
    })
}

